@background_color: pink;
@card_primary_color: white;
@text_color: gray;

@shadow: 0px 1px 2px 1px #DDDDDD;

@card_translate_distance: 1.5rem;

@initial_animation_duration: 1s;
@loop_animation_duration: 1s;

@phones: ~"all and (max-width: 360px)";

// https://gist.github.com/MoOx/9137295
.reset_button() {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;

  background: transparent;

  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;

  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;

  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;

  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;

  /* Remove excess padding and border in Firefox 4+ */
  &::-moz-focus-inner {
    border: 0;
    padding: 0;
  }

  /* my own styles */
  cursor: pointer;
  text-decoration: none;
}
